<template>
	<div>
		<div class="card">
			<h3> Title №{{ currentPost.id }} </h3>
			<p> {{ currentPost.title }} </p>
			<img :src="currentPost.thumbnailUrl" alt="image">
		</div>
		<router-link to="/">
			Back to main page
		</router-link>
	</div>
</template>

<script>

	import { mapGetters } from 'vuex';
	
	export default {
		name: 'Post',
		data() {
			return {

			}
		},
		computed: {
			...mapGetters(['post']),
			id() {
				return this.$route.params.id;
			},
			currentPost() {
				return this.post(this.id);
			}
		},
		created() {
			console.log(this.$route);
		}
	};

</script>